:root {
  --product-card-picture-mask-color: #f5f5f5;
  --product-card-color-border-color: #efefef;
  --product-cart-picture-label-background: rgba(255, 255, 255, .7); }

.ProductCard {
  padding-left: 0;
  min-width: 0; }
  .ProductCard::before {
    content: none; }
  .ProductCard:last-child {
    margin-bottom: .72rem; }
    @media (max-width: 810px) {
      .ProductCard:last-child {
        margin-bottom: .84rem; } }
  .ProductCard-Content {
    padding: 1.2rem;
    display: flex;
    flex-wrap: wrap;
    padding-top: 23px; }
    @media (max-width: 810px) {
      .ProductCard-Content {
        padding: 23px 1.4rem 1.4rem; } }
  .ProductCard-Name, .ProductCard-Brand {
    width: 100%;
    font-size: 1.08rem;
    max-width: 100%;
    overflow: hidden;
    white-space: nowrap; }
    @media (max-width: 810px) {
      .ProductCard-Name, .ProductCard-Brand {
        font-size: 1.26rem; } }
    .ProductCard-Name_isLoaded, .ProductCard-Brand_isLoaded {
      text-overflow: ellipsis; }
    @media (min-width: 811px) {
      .ProductCard-Name, .ProductCard-Brand {
        font-size: 1.4rem;
        line-height: 1.3; } }
  @media (min-width: 811px) {
    .ProductCard-Name {
      margin-top: .6rem; } }
  .ProductCard-Brand {
    font-weight: 300;
    opacity: .5; }
    @media (max-width: 810px) {
      .ProductCard-Brand {
        line-height: 1;
        font-size: 1.2rem; } }
  .ProductCard-Picture {
    padding-bottom: 120%; }
  .ProductCard-PictureMissing {
    position: absolute;
    text-align: center;
    width: 100%;
    height: 20px;
    bottom: 0;
    margin: auto;
    top: 0; }
  .ProductCard-Reviews {
    --product-rating-size: 1.2rem;
    width: 100%; }
  .ProductCard-ConfigurableOptions {
    flex-grow: 1;
    display: flex;
    justify-content: flex-end;
    align-items: center; }
  .ProductCard-Color {
    --product-card-color-size: 9px;
    display: inline-block;
    width: var(--product-card-color-size);
    height: var(--product-card-color-size);
    border-radius: 50%;
    overflow: hidden;
    margin-left: calc(var(--product-card-color-size) / 3);
    border: 1px solid var(--product-card-color-border-color); }
    @media (min-width: 811px) {
      .ProductCard-Color {
        --product-card-color-size: 10px; } }
  @media (min-width: 811px) {
    .ProductCard-Price {
      font-size: 2rem; } }
  .ProductCard-PriceBadge {
    color: var(--secondary-dark-color);
    margin-right: 5px;
    font-size: 1rem;
    position: absolute;
    top: 7px; }
  .ProductCard-Figure {
    flex-grow: 1; }
  .ProductCard-Link {
    color: inherit;
    display: flex;
    flex-direction: column; }
    .ProductCard-Link:hover, .ProductCard-Link:focus {
      text-decoration: none; }
  .ProductCard p {
    margin-bottom: 0; }
  .ProductCard .TextPlaceholder,
  .ProductCard .Image_isPlaceholder {
    opacity: 0;
    animation: appear 100ms 300ms forwards; }
