.TierPrices {
  width: 100%; }
  .TierPrices-Item {
    font-size: 1.5rem;
    padding-left: 0; }
    @media (min-width: 811px) {
      .TierPrices-Item {
        padding-left: 5px; } }
    .TierPrices-Item:last-child {
      margin-bottom: 1.2rem; }
      @media (max-width: 810px) {
        .TierPrices-Item:last-child {
          margin-bottom: 1.4rem; } }
    .TierPrices-Item::before {
      content: none; }
    .TierPrices-Item_isLowest {
      font-size: 1.2rem;
      margin-top: 2px;
      padding: 0; }
      .TierPrices-Item_isLowest:last-child {
        margin-bottom: 0; }
  .TierPrices-ItemPrice {
    font-weight: bold; }
