:root {
  --price-color: #000;
  --price-discount-color: #000; }

.ProductPrice {
  color: var(--price-color);
  font-weight: 700;
  line-height: 1;
  vertical-align: middle; }
  @media (max-width: 810px) {
    .ProductPrice {
      font-size: 1.8rem; } }
  .ProductPrice ins {
    text-decoration: none; }
  .ProductPrice del {
    font-size: .7em;
    font-weight: 500;
    opacity: .5; }
  .ProductPrice-HighPrice {
    padding-left: .1em;
    color: var(---price-discount-color);
    display: none; }
    .ProductPrice-HighPrice_isVisible {
      display: block; }
  .ProductPrice-Placeholder::after {
    content: 'aaaaa'; }
  .ProductPrice-SubPrice {
    display: block;
    font-weight: 500;
    font-size: .7em; }
