:root {
  --product-rating-background: #979797;
  --product-rating-filled-background: var(--primary-base-color);
  --product-rating-size: 1.8rem; }
  @media (max-width: 810px) {
    :root {
      --product-rating-size: 2.1rem; } }

.ProductReviewRating {
  --percentage: 0%;
  display: inline-block;
  font-size: var(--product-rating-size);
  letter-spacing: 2px; }
  .ProductReviewRating::before {
    content: '★★★★★';
    background: linear-gradient(90deg, var(--product-rating-filled-background) var(--percentage), var(--product-rating-background) var(--percentage));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent; }
  .ProductReviewRating_isLoading::before {
    background-image: var(--placeholder-image);
    background-size: var(--placeholder-size);
    animation: var(--placeholder-animation); }
